.logo {
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 64px;
  &__img {
    width: 30px;
    height: 30px;
  }

  &__wrap {
    border-radius: 6px;
    padding: 8px 20px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 8px;
  }

  &__name {
    font-size: 3rem;
    font-weight: 700;
    color: #ffff;
  }

  &__highlight {
    animation: gradientAnimation 5s ease infinite;
    color: #ffd43b;
  }
}
@keyframes gradientAnimation {
  0% {
    color: #000000; /* Black color */
  }
  50% {
    color: #c0ac77; /* Yellow color */
  }
  100% {
    color: #ffb900; /* Yellow color */
  }
}
